<template>
  <el-row>
    <el-col :span="3">
   <img src="../assets/cnodejs_light.svg" alt="" class="logo">
    </el-col>
   <el-col :span="5"> 
        <el-input size="mini" placeholder="请输入内容" >
        <i slot="prefix" class="el-input__icon el-icon-search"></i>
      </el-input>
    </el-col>
    <el-col :span="3" :offset="13" v-if="!logined">
      <el-button type="text" class="my-btn" @click="login">登录</el-button>
      <el-button type="text" class="my-btn">注册</el-button>
    </el-col>
    <el-col v-if="logined" class="hidden-xs-only" :span="3" :offset="13">
      <img style="vertical-align: middle;" :src="userInfos&&userInfos.avatar_url" height="30" alt=""> <a href="#" style="color:#fff;">{{userInfos&&userInfos.loginname}}</a> <a href="javascript:;" @click="logout" style="color:red;">退出</a>
    </el-col>
  </el-row>
</template>
<script>
import {mapState} from 'vuex'
export default {
  name: "app-header",
  computed:{
      ...mapState([
          'logined',
          'userInfos'
      ])
  },
  methods:{
    login(){
      this.$router.push({
        path:'/login'
      })
    },
    logout(){
          this.$cookie.delete('token_id');
          this.$router.push({
            path:'/all'
          })
          window.location.reload();
      }

  }

};
</script>
<style>
.my-btn {
  color: #fff;
}
.head-search{
 background-color: red;
}
.el-input input{
  background-color:rgba(0, 0, 0, .2);
  border-radius: 15px;
  transition:1s;
}
.el-input input:hover{
  background-color:white;
}
</style>
 


