<template>
  <el-button-group class="btn-box">
    <el-button  @click="prev"  size="small" type="primary" icon="el-icon-arrow-left">上一页</el-button>
    <el-button  @click="next" size="small"  type="primary">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
</el-button-group>
</template>
<script>
import {mapState,mapMutations,mapActions}from 'vuex'
export default {
  name:'page',
  components:{},
  computed:{
   ...mapState([
     'page'
   ])
  },
  methods:{
    ...mapActions([
      'getList'
    ]),
    ...mapMutations([
      'editPage'
    ]),
    prev(){
      if(this.$route.params.page-1<=0){

              this.$message({
                message:'目前已经到达第一页',
                type:'warning',
                duration:1000
              });
              return false;
            }

          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });

          this.$router.push({path:`/${this.$route.name}/${this.$route.params.page-1}`});
          this.editPage(this.$route.params.page);
          this.getList()
            .then(()=>{
              loading.close();
            });
    },
            next(){



          if(this.$route.params.page- -1>this.pagenum){

            this.$message({
              message:'已到达最后一页',
              type:'warning',
              duration:1000
            });
            return false;
          }

          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });

//            console.log(this.$router,this.$route);
          //编程式路由跳转
          this.$router.push({path:`/${this.$route.name}/${this.$route.params.page- -1}`});

          //修改store中的page
          this.editPage(this.$route.params.page);
          //请求内容
          this.getList()
            .then(()=>{
              loading.close();
            });
        }
  }
}
</script>
<style>
.btn-box{
    margin-left: 370px;
    margin-top: 20px;
}
</style>



