<template>

  <el-container>
      <el-header class="my-header">
          <app-header></app-header>
      </el-header>
     <el-main class="my-main">
        <item></item>
        <warp>
          <router-view slot="route_list"></router-view>
        </warp>    
      
      </el-main>
  </el-container>


</template>
<script>
import appHeader from './header'
import item from './item'
import warp from './warp'

export default {
  name:"index",
  components:{
      appHeader,
      item,
      warp
     
  }

}
</script>

<style>


  .my-header{
    background-color: #444;
    width:100%;
    color:#fff;
    line-height:60px;
  }
  .logo{
    height:30px;
    margin-top:15px;
  }

</style>



