<template>
<div>
<el-row>
 
 <el-menu class="nav"
      :default-active="list_of"
      :router="true"
    >
      <el-menu-item route="/all" index="all" color="red">全部</el-menu-item>
      <el-menu-item route="/good" index="good">精华</el-menu-item>
      <el-menu-item route="/share" index="share">分享</el-menu-item>
      <el-menu-item route="/ask" index="ask">问答</el-menu-item>
      <el-menu-item route="/job" index="job">招聘</el-menu-item>
    </el-menu>
     <el-button class="release" @click="to_topic" v-if="logined" type="success">发布主题</el-button>
</el-row>
</div>
</template>
<script>
// import list from './list'
import {mapState} from 'vuex'
export default {
  name:"item",
  components:{
    // list
  },
  computed:{
        ...mapState([
            'list_of',
            'logined',
        ])
  },
  methods:{
    to_topic(){
      this.$router.push({
        path:"/topic/"+this.$route.name
      })
    }
  }
}
</script>
<style>
.list-nav{
  margin-bottom: 20px;
}
.nav{
  border:none;
  margin-bottom: 10px;
  background: transparent;
  padding-left: 20px;
}
.nav li{
    float: left;
    width: 60px;
    border: 1px solid skyblue;
    margin: 0 5px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    border-radius: 5px;
    padding: 0 45px;
}
.release{
  position:absolute;
  right: 10px;
  top:-5px;
}
</style>




