import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)


var config = {
    rootUrl:'https://cnodejs.org/api/v1',
    topics:'/topics',//列表请求地址
    topic:'/topic' ,//详情请求地址
    useinfo:'/user'//用户详情
};


export default new Vuex.Store({
    state:{
        list_of:"all",
        list:[],
        page:1,
        pageData:null,
        pageContent:null,
        pageReplies:null,
        accessToken:'06875cad-5e0f-4e0c-b3fd-a22966252f65',
        userInfo:{
            username:'zhaizhixuan',
            password:'123'
          },
        userInfos:null,
        logined:false,
        postContent:{
            title:'',
            tab:'',
            content:''
        },
        topic_id:''
    },
    getters:{},
    mutations:{
        editList(state,list){
            state.list=list; //修改state里的list的数据  数据是请求来的  这是一个方法
        },
        eiitListOf(state,name){
            state.list_of=name;//修改state里的list_of的数据  更改路由
        },
        editPage(state,num){
            state.page=num;
        },
        editPageData(state,data){
            state.pageData=data;
            state.pageContent=data.content;
            state.pageReplies = data.replies;
        },
        editLogined(state,bool){
            state.logined = bool;
          },
        edituserInfos(state,infos){
            state.userInfos = infos;
        },
        editPostContent(state,obj){
           Object.assign(state.postContent,obj);
        },
        editTopicId(state,id){
            state.topic_id = id;
        }
    },
    actions:{
        //请求数据40条
        getList(context){
            let {rootUrl,topics} = config;// 解构赋值
            return axios.get(rootUrl+topics,{
                params:{
                    tab:context.state.list_of,
                    page:context.state.page
                }
            })
            .then(data=>{
                // console.log(data)
                context.commit('editList',data.data.data)
            
            })
        },
        //请求详情
        getData(context,id){
            var {rootUrl,topic} = config;
      
            return axios.get(rootUrl+topic+'/'+id)
              .then(data=>{
                context.commit('editPageData',data.data.data);
                
              });
          },
          //请求用户 登录
          getUserInfos(context){
            var {rootUrl,useinfo} = config;
            return axios.get(rootUrl+useinfo+'/'+context.state.userInfo.username)
            .then(data=>{
                console.log(data.data.data);
                context.commit('edituserInfos',data.data.data);
            })
          },
          //请求发送
          sendContent(context){
            
            let {rootUrl,topics}=config;
            
            let obj =Object.assign(context.state.postContent,{accesstoken:context.state.accessToken})
            console.log(obj);
            
            return axios.post(rootUrl+topics,obj)
            .then(data=>{
                context.commit('editTopicId',data.data.topic_id)
            })
          }

    }


})


