<template>
<el-container>
<el-main class="itembox">
      <slot name="route_list"></slot>
</el-main>
 <el-aside>
      <app_aside></app_aside>
</el-aside>
</el-container>
</template>
<script>
import app_aside from './aside'
import item from './item'
export default {
  name:'warp',
  components:{
    app_aside,
    item
  }
}
</script>
<style>
.itembox{
  padding-top: 0;
}
</style>




