<template>
   <el-container>
       <el-header class="my-header">
          <app-header></app-header>
      </el-header>
      <el-row class="topicebox">
        <el-col :span="18" :offset="3">
          标题:
           <el-input id="inputcon" v-model="title" placeholder="请输入内容" style="width:400px;"></el-input>
        </el-col>
         <el-col :span="18" :offset="3" id="inputsel">
           类型： 
             <el-select v-model="mes" placeholder="请选择">
                <el-option value="dev"></el-option>
                <el-option value="job"></el-option>
                <el-option value="share"></el-option>
                <el-option value="ask"></el-option>
           </el-select>
         </el-col>    
         <el-col :span="18" :offset="3">
        内容：
        <el-input
          v-model="content"
          style="width:80%;"
          type="textarea"
          :rows="10"
          placeholder="请输入内容"
        >
        </el-input>

    </el-col>
    <el-col :span="18" :offset="3">
        <el-button @click="send" type="success" style="margin-left:70px;margin-top:20px;">提交主题</el-button>
    </el-col>
      </el-row>
   </el-container>
</template>

<script>
import {mapState,mapMutations,mapActions} from 'vuex'
import appHeader from "./header";
    export default {
        name:"topic",
        data(){
          return {
              title:'',
              content:'',
              mes:'dev'
          }
        },
        components: {
             appHeader
        },
        computed:{
          ...mapState([
            'topic_id'
            ]),
            tab(){
              if(this.$route.params.name=='all'||this.$route.params.name=='good'){
                this.mes='dev';
                // this.editPostContent(this.mes)
              }else{
                this.mes = this.$route.params.name
                // this.editPostContent(this.mes);
              }
              return this.mes;
            },
        },
        
        methods:{
        ...mapMutations([
          'editPostContent'
        ]),
        ...mapActions([
          'sendContent'
        ]),
        send(){
          
          //验证
          if(this.title==''){
            this.$message({
              type:'error',
              message:'标题不能为空'
            })
          }else if(this.title.length<3){
            this.$message({
              type:'error',
              message:'标题不能太短'
            })
          }else if(this.content==""){
             this.$message({
              type:'error',
              message:'内容不能为空'
            })
          }else{
            //成功了 
           
            // this.editPostContent({title:this.title,tab:this.tab,content:this.content});
            this.editPostContent({ //同步数据到store
                title:this.title,
                tab:this.mes,
                content:this.content
            });
            
            //提交交互
             const loading = this.$loading({
                lock: true,
                text: '数据发送中...',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
              });
              
             
              this.sendContent()
              .then(data=>{
                    loading.close();
                      this.$message({
                      type:'success',
                      message:'发送成功',
                      duration:1000
                    })
                      this.$router.push({
                      path:'/page/'+this.topic_id
                      // path:'/all'
                      
                    });
                })
                // 发送服务失败
                .catch(()=>{
                   loading.close();
                    this.$message({
                      type:'error',
                      message:'提交失败！',
                      duration:1000
                    });
                })

          }
        }
        },
        beforeMount(){
          this.mes=this.tab
        }
        
    }
</script>

<style>
.login_input {
  margin: 10px 0;
  background-color: #ffffff;
}
.btn {
  margin-top: 10px;
}
.my-header {
  background-color: #444;
  width: 100%;
  color: #fff;
  line-height: 60px;
}
.logo {
  height: 30px;
  margin-top: 15px;
}
.topicebox{
  padding-top: 40px;
}
#inputcon{
  background-color: transparent;
  margin-bottom: 20px;
  margin-left: 10px;
}
#inputsel input{
  background-color: transparent;
   margin-bottom: 20px;
}
</style>


